import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import RegularCard from "../layouts/cards/RegularCard";
import ItemGrid from "../layouts/grid/ItemGrid";
import WorkExperienceCard from "../layouts/cards/WorkExperienceCard";
import Typography from "@material-ui/core/Typography";

const styles = theme => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

function WorkExperienceComponent(props) {
  const { classes } = props;
  return (
    <Grid container className={classes.root}>
      <ItemGrid item xs={12} sm={12} md={12}>
        <RegularCard
          cardTitle={"Work Experience"}
          cardSubtitle={"Jan 2015 - Present"}
          headerColor={"green"}
          content={
            <Grid container>
              <ItemGrid item xs={12} sm={12} md={4}>
                <WorkExperienceCard
                  logo={""}
                  title={"Wipro Technologies Pvt Ltd, Bangalore"}
                  subtitle={"April, 2017 - Present"}
                  description={
                    `Wipro Limited is an Indian multinational corporation that provides information technology, consulting and business process services. It is headquartered in Bangalore, Karnataka, India. In 2013, Wipro separated its non-IT businesses and formed the privately owned Wipro Enterprises.`
                  }
                  companyUrl={"https://www.wipro.com/"}
                  experience={
                    <React.Fragment>
                      <Typography variant="h6">Work Experience:</Typography>
                      <Typography paragraph>
                      </Typography>
                      <Typography paragraph>
                        Web based application development utilizing Perl.
                      </Typography>
                      <Typography paragraph>
                        Have to add new features in the application, Monitor and maintenance of the application.
                      </Typography>
                      <Typography paragraph>
                        Discussing business requirements with clients and preparing technical specification documents.
                      </Typography>
                      <Typography paragraph>
                        Responsible for analyzing, designing and coding of the application using Perl, HTML, CSS, Javascript and SQL queries.
                      </Typography>
                      <Typography paragraph>
                        Dealing with technical problems and fixing bugs.
                      </Typography>
                      <Typography paragraph>
                        Provided estimates and release plans.
                      </Typography>
                      <Typography paragraph>
                        Working knowledge in version control system (Git).
                      </Typography>
                    </React.Fragment>
                  }
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <WorkExperienceCard
                  logo={""}
                  title={"Merit Software Services Pvt Ltd, Chennai"}
                  subtitle={"April, 2016 - March, 2017"}
                  description={
                    `Merit is a tight-knit team of tech lovers and problem solvers who thrive on getting a job done well, on time and on budget.Our combination of exceptional talent, flexible resources and cost-effective delivery sees us power some of the worlds most trusted B2B data products.`
                  }
                  companyUrl={"https://www.meritgroup.co.uk/"}
                  experience={
                    <React.Fragment>
                      <Typography variant="h6">Work Experience:</Typography>
                      <Typography paragraph>
                      </Typography>
                      <Typography paragraph>
                        Developed the automation applications for the clients using Perl and Python scripting.
                      </Typography>
                      <Typography paragraph>
                        Handled the development and maintenance phase of projects.
                      </Typography>
                      <Typography paragraph>
                        Created test cases for unit testing.
                      </Typography>
                    </React.Fragment>
                  }
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <WorkExperienceCard
                  logo={""}
                  title={"Scope e-Knowledge Center Pvt Ltd, Chennai"}
                  subtitle={"January, 2015 - March, 2016"}
                  description={
                    `Scope e-Knowledge Center is a one of the largest offshore providers of integrated knowledge services and solutions to the Professional Publishing (Business, Legal & STM) and information industry (Information & Media) across the world.`
                  }
                  companyUrl={"https://www.scopeknowledge.com/"}
                  experience={
                    <React.Fragment>
                      <Typography variant="h6">Work Experience:</Typography>
                      <Typography paragraph>
                      </Typography>
                      <Typography paragraph>
                        Researched, designed, and developed enhanced applications and met business criteria.
                      </Typography>
                      <Typography paragraph>
                        Developed a software for automating production work for few clients.
                      </Typography>
                      <Typography paragraph>
                        Developed help files, user manuals, and database connectivity libraries.
                      </Typography>
                      <Typography paragraph>
                        Developed, tested, delivered, and installed application at clients' server.
                      </Typography>
                      <Typography paragraph>
                        Provided analytical support to the clients.
                      </Typography>
                    </React.Fragment>
                  }
                />
              </ItemGrid>
            </Grid>
          }
        />
      </ItemGrid>
    </Grid>
  );
}

WorkExperienceComponent.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(WorkExperienceComponent);
