import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import RegularCard from "../layouts/cards/RegularCard";
import ItemGrid from "../layouts/grid/ItemGrid";
import ProjectCard from "../layouts/cards/ProjectCard";

const styles = theme => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

function ProjectsComponent(props) {
  const { classes } = props;
  return (
    <Grid container className={classes.root}>
      <ItemGrid item xs={12} sm={12} md={12}>
        <RegularCard
          cardTitle={"Projects"}
          headerColor={"orange"}
          content={
            <Grid container>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"RAMP"}
                  technology={"Technologies Used : Perl/HTML/CSS/Javascript/MySQL "}
                  description={
                    "Application used to simulate the product feature before get into a actual manufacturing"
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"Glenigan"}
                  technology={"Technologies Used : Perl/Oracle "}
                  description={
                    "Collect the construction based application details that applied in UK and the download that application form that maintained in a cloud based on the application number for the future purpose. "
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"HSJ"}
                  technology={"Technologies Used : Perl/Oracle/Excel "}
                  description={
                    "It is a tool to track and manage the data arrival details within a given schedule time from the client. "
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"Links Extraction"}
                  technology={"Technologies Used : Perl/MySQL "}
                  description={
                    "Tool to collect the data from the given links. "
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"Portfolio"}
                  technology={"Technologies Used :  ReactJS/Redux/HTML/CSS "}
                  description={
                    "Created own portfolio using ReactJs also Working in various sample applications."
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"CDF Hospital"}
                  technology={"Technologies Used : PHP/MySQL "}
                  description={
                    "Design and develop the website for CDF hospital and done the payment gateway for patient appointments."
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"Shopping Snoop"}
                  technology={"Technologies Used : PHP/HTML/CSS "}
                  description={
                    "The main purpose of this project is to provide the shops and products details in shopping mall. The users can easily identify and purchase the products in a specific mall. "
                  }
                  siteUrl={""}
                />
              </ItemGrid>
              <ItemGrid item xs={12} sm={12} md={4}>
                <ProjectCard
                  title={"Research on Sentimental Analysis"}
                  technology={"Technologies Used : Deep learning/ Tensor Flow "}
                  description={
                    "Have done research work in text processing and image processing. "
                  }
                  siteUrl={""}
                />
              </ItemGrid>
            </Grid>
          }
        />
      </ItemGrid>
    </Grid>
  );
}

ProjectsComponent.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ProjectsComponent);
