import React from "react";
import Grid from "@material-ui/core/Grid";
import ProfileCard from "../layouts/cards/ProfileCard";
import ItemGrid from "../layouts/grid/ItemGrid";
import avator from "../../assets/images/ProfileImage.jpg";
import DownloadComponent from "./DownloadComponent";

function ProfileComponent() {
  return (
    <Grid container>
      <ItemGrid item xs={12} sm={12} md={12}>
        <ProfileCard
          avatar={avator}
          subtitle={"Software Developer"}
          title={"Mynavathi Thangaraji"}
          description={
            "Looking for a challenging role in a reputable organization to utilize my skills for the growth of the organization as well as to enhance my knowledge about new and emerging trends in the IT sector."
          }
          footer={<DownloadComponent />}
        />
      </ItemGrid>
    </Grid>
  );
}

export default ProfileComponent;
